	module.exports = {"data":{"markdownRemark":{"html":"<p>两位报考博士生的学生来找，问考事项。我说，博士考试已经不是教科书式的了，要依靠学生平常积累，临时准备不行。问题是在日常的学习中积累了多少知识，有多宽的知识面。研究生，特别是博士研究生要培养创造性，而不是对死的知识的记忆。不然就不可能培养出优秀学生。这是一个方向。第二就是注意逻辑分析的能力，要有较强的理性的推理能力，对事物要有足够的敏感性，要能够在一般的知识中发现可以创造的点。三是要有一个比较健全的人格，没有健全的人格是培养不出真正杰出的学生的，杰出不仅对个人而言，也是对社会和学术精神而言的。这是我们今后培养学生要注意的战略方向。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0055.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0055.md","frontmatter":{"title":"3月11日 星期五"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0055.md absPath of file >>> MarkdownRemark"}}

